const TEXT_ELEMENT = 'TEXT ELEMENT'; // ç±»å‹
const stateQueue = [];
const componentQueue = [];

// è§¦å‘æ›´æ–°çš„æ–¹æ³•
// 1. ReactDOM.render()  é€šå¸¸åªä¼šåœ¨å…¥å£æ–‡ä»¶æ‰§è¡Œä¸€æ¬¡
// 2. component.setState
//   - DidactDOM.updateComponent

const flush = () => {
  let item, component;
  // è®¡ç®—æ¯ä¸ªç»„ä»¶çš„state
  while ((item = stateQueue.shift())) {
    const { partialState, component } = item;
    if (!component.prevState) component.prevState = { ...component.state };
    if (typeof partialState === 'function') {
      component.state = {
        ...component.state,
        ...partialState(component.prevState, component.props),
      };
    } else {
      component.state = { ...component.state, ...partialState };
    }
    component.prevState = component.state;
  }
  // æ¸²æŸ“æ¯ä¸ªç»„ä»¶
  while ((component = componentQueue.shift())) {
    DidactDOM.updateComponent(component);
  }
};
class Component {
  constructor(props) {
    this.state = {};
    this.props = props;
  }
  render() {
    // å­ç±»ç»§æ‰¿
  }
  setState(partialState) {
    // å¼‚æ­¥
    if (stateQueue.length === 0) {
      Promise.resolve().then(() => flush());
    }
    stateQueue.push({
      partialState,
      component: this,
    });
    if (componentQueue.indexOf(this) === -1) {
      componentQueue.push(this);
    }
  }
}

const Didact = {
  createElement,
  Component,
};

// Didactå…ƒç´  {type,key, props}
// JSX babelè‡ªåŠ¨è°ƒç”¨ ç”ŸæˆDidactå…ƒç´ 
// children: [[{}, {}, {}]] (å½“ä½¿ç”¨mapçš„æ—¶å€™)
// æˆ–è€… [{}, {}]
function createElement(type, config = {}, ...args) {
  const { key, ...props } = { key: '', ...config };
  // const newChildren = children.map();
  // æ’é™¤undefinedå’Œnullã€false
  //<div>hello {name}</div> ä¼šæœ‰ä¸¤ä¸ªchildï¼Œ {name}å¯èƒ½ä¸ºundefined
  // false: {count && <div>hello</div>}
  const hasChildren = args.length > 0;
  const rawChildren = hasChildren ? [].concat(...args) : []; // concatå¯ä»¥åˆå¹¶æ•°ç»„æˆ–å€¼

  props.children = rawChildren
    .filter(c => c != null && c !== false)
    .map(c => (c instanceof Object ? c : createTextElement(c)));
  return {
    type,
    key,
    props,
  };
}
// æ ¼å¼åŒ–æ–‡æœ¬ç±»å‹
function createTextElement(value) {
  // è§„èŒƒæ•°æ®
  return createElement(TEXT_ELEMENT, { nodeValue: value });
}

///////////////////////////////////////////////////////////////////////////////DidactDOM
let rootInstance = null;
//   const instance = { dom, element, childInstances };
const DidactDOM = {
  // æ¸²æŸ“å…¥å£
  render(element, container) {
    // elementæ˜¯JSXå¯¹è±¡
    const prevInstance = rootInstance;
    const nextInstance = reconcile(container, prevInstance, element);

    rootInstance = nextInstance;
  },

  // æ›´æ–°ç»„ä»¶
  updateComponent(component) {
    const internalInstance = component.__internalInstance;

    reconcile(internalInstance.dom.parentNode, internalInstance, internalInstance.element);
  },
  setComponentProps(component, props) {
    component.props = props;
    return this.updateComponent(component);
  },
};

/**
 *
 * @param {*} parentDom
 * @param {*} instance å½“å‰çš„instanceå¯¹è±¡
 * @param {*} element
 * @return ç”Ÿæˆæ–°çš„instanceï¼ˆvdomï¼‰
 */
// 1. æ²¡æœ‰vdom, åˆ›å»º
// 2. å·²ç»æœ‰vdomï¼Œ æ›´æ–°è§¦å‘
// --- å¯¹æ¯”-è™šæ‹Ÿdomæ ‘ï¼Œ è¿›è¡Œå¢ã€åˆ ã€æ”¹ï¼ˆç±»å‹ç›¸åŒï¼‰ã€æ›¿æ¢ï¼ˆç±»å‹ä¸åŒï¼‰
function reconcile(parentDom, instance, element) {
  if (!instance) {
    // æ–°å¢
    const newInstance = instantiate(element);
    parentDom.appendChild(newInstance.dom);
    return newInstance;
  } else if (!element) {
    // åˆ é™¤
    parentDom.removeChild(instance.dom);
    return null;
  } else if (instance.element.type === element.type) {
    // ç›¸åŒç±»å‹

    // domåŸå§‹ç±»å‹
    if (typeof element.type === 'string') {
      // æ›´æ–°å±æ€§
      updateDomProperties(instance.dom, instance.element.props, element.props);
      // æ›´æ–°å­å…ƒç´ 
      instance.childInstances = reconcileChildren(instance, element);
      instance.element = element;
      return instance;
    } else {
      // ç»„ä»¶ç±»å‹
      // æ›´æ–°ç»„ä»¶props
      const component = instance.component;
      component.props = element.props;
      // é‡æ–°renderç»„ä»¶,è·å–vdom
      const childElement = component.render();
      const oldChildInstance = instance.childInstance;
      const childInstance = reconcile(parentDom, oldChildInstance, childElement); // å¯¹æ¯”-å‰©ä¸‹-å­©å­
      instance.dom = childInstance.dom;
      instance.childInstance = childInstance;
      instance.element = element;
      return instance;
    }
  } else {
    // ç±»å‹ä¸åŒï¼Œç›´æ¥æ›¿æ¢
    const newInstance = instantiate(element);
    parentDom.replaceChild(newInstance.dom, instance.dom);
    return newInstance;
  }
}

// function reconcileChildren(instance, element) {
//   const parentDom = instance.dom;
//   const childInstances = instance.childInstances;
//   const childElements = element.props.children || [];
//   const count = Math.max(childInstances.length, childElements.length);
//   const newChildInstances = [];

//   for (let i = 0; i < count; i++) {
//     // æœ‰å¯èƒ½æ–°å¢æˆ–å‡å°‘å­å…ƒç´ 
//     const newChildInstance = reconcile(parentDom, childInstances[i], childElements[i]);
//     newChildInstances.push(newChildInstance);
//   }
//   return newChildInstances.filter(instance => instance != null); // è¿‡æ»¤æ‰åˆ é™¤çš„å…ƒç´ 
// }
function reconcileChildren(instance, element) {
  const parentDom = instance.dom;
  const childInstances = instance.childInstances;
  const childElements = element.props.children || [];
  const count = Math.min(childInstances.length, childElements.length); // æœ€å°å€¼
  console.log({ count });
  const newChildInstances = [];

  let i = 0;

  // ç¬¬ä¸€è½®éå†
  for (; i < count; i++) {
    const childInstance = childInstances[i];
    const oldElement = childInstance.element;
    const childElement = childElements[i];
    if (oldElement.key === childElement.key) {
      if (oldElement.type === childElement.type) {
        // å¯ä»¥å¤ç”¨
        const newChildInstance = reconcile(parentDom, childInstance, childElement);
        newChildInstances.push(newChildInstance);
      } else {
        // typeä¸åŒï¼Œåˆ é™¤åŸæ¥çš„å…ƒç´  todo
      }
    } else {
      // key ä¸åŒï¼Œè·³å‡ºç¬¬ä¸€è½®éå†
      console.log('break');
      break;
    }
  }
  let lastIndex = i - 1;
  // å°†å‰©ä¸‹çš„childInstancesæŒ‰kyè¿›è¡Œmap
  const existingChildren = {};
  for (; i < childInstances.length; i++) {
    existingChildren[childInstances[i].element.key] = {
      instance: childInstances[i],
      index: i,
    };
  }

  for (let i = lastIndex + 1; i < childElements.length; i++) {
    const childElement = childElements[i];
    const item = existingChildren[childElement.key];
    if (item) {
      const mountIndex = item.index;
      const newChildInstance = reconcile(parentDom, item.instance, childElement);

      if (lastIndex < mountIndex) {
        // ä¸åŠ¨
        newChildInstances.push(newChildInstance);
      } else {
        // ç§»åŠ¨åˆ°æœ€å
        parentDom.appendChild(newChildInstance.dom);
        // Node.appendChild() æ–¹æ³•å°†ä¸€ä¸ªèŠ‚ç‚¹é™„åŠ åˆ°æŒ‡å®šçˆ¶èŠ‚ç‚¹çš„å­èŠ‚ç‚¹åˆ—è¡¨çš„æœ«å°¾å¤„ã€‚
        // å¦‚æœå°†è¢«æ’å…¥çš„èŠ‚ç‚¹å·²ç»å­˜åœ¨äºå½“å‰æ–‡æ¡£çš„æ–‡æ¡£æ ‘ä¸­ï¼Œ
        // é‚£ä¹ˆ appendChild() åªä¼šå°†å®ƒä»åŸå…ˆçš„ä½ç½®ç§»åŠ¨åˆ°æ–°çš„ä½ç½®ï¼ˆä¸éœ€è¦äº‹å…ˆç§»é™¤è¦ç§»åŠ¨çš„èŠ‚ç‚¹ï¼‰ã€‚
      }
      lastIndex = Math.max(lastIndex, mountIndex);
    } else {
      // æ–°å¢
      const newChildInstance = instantiate(childElement);
      if (childInstances[lastIndex + 1]) {
        parentDom.insertBefore(newChildInstance.dom, childInstances[lastIndex + 1].dom);
      } else {
        // æœ€å
        parentDom.appendChild(newChildInstance.dom);
      }
      newChildInstances.push(newChildInstance);
    }
    delete existingChildren[childElement.key];
  }
  // éå†åŸæ¥æœªå¤„ç†çš„å…ƒç´ ï¼Œ åˆ é™¤
  Object.values(existingChildren)
    .map(item => item.instance)
    .forEach(instance => {
      parentDom.removeChild(instance.dom);
    });
  return newChildInstances.filter(instance => instance != null);
}

// åˆ›å»ºinstanceå¯¹è±¡
// æ–°å»ºè™šæ‹Ÿdomç»„ä»¶  instance:{dom, element, childInstance, component}
// æ–°å»ºè™šæ‹Ÿdomå…ƒç´  instance: {dom, element, childInstances}
function instantiate(element) {
  const { type, props } = element;
  // ---ç»„ä»¶ç±»å‹
  if (typeof type === 'function') {
    const instance = {};

    const component = createComponent(element, instance); // ç»„ä»¶å®ä¾‹
    const childElement = component.render(); // è°ƒç”¨ç»„ä»¶renderï¼Œè·å¾— ç»„ä»¶ element
    const childInstance = instantiate(childElement);
    const dom = childInstance.dom;
    Object.assign(instance, {
      dom, // ç»„ä»¶dom
      element, // jsxå¯¹è±¡
      childInstance, // ç»„ä»¶renderåçš„element instance, æ²¡æœ‰childInstances
      component, // ç»„ä»¶å®ä¾‹
    });
    return instance;
  }
  // ---domå…ƒç´ 
  const dom = type === TEXT_ELEMENT ? document.createTextNode('') : document.createElement(type);

  updateDomProperties(dom, [], props);

  const childInstances = (props.children || []).map(instantiate);
  const childDoms = childInstances.map(childInstance => childInstance.dom);
  childDoms.forEach(child => dom.appendChild(child));
  return {
    dom,
    element,
    childInstances,
  };
}

function updateDomProperties(dom, prevProps, nextProps) {
  // åˆ é™¤å·²ç»åˆ é™¤çš„å±æ€§
  for (let key in prevProps) {
    if (!key in nextProps) {
      removeAttribute({ key, value: prevProps[key], dom });
    }
    if (isEvent(key)) {
      removeAttribute({ key, value: prevProps[key], dom });
    }
  }
  // å¦‚æœå±æ€§æ”¹å˜ï¼Œ è®¾ç½®æ–°çš„å±æ€§
  for (let key in nextProps) {
    // console.log({ key });
    if (nextProps[key] !== prevProps[key]) {
      setAttribute({ key, value: nextProps[key], dom });
    }
  }
}

const isEvent = name => name.startsWith('on');

function removeAttribute({ key, value, dom }) {
  // todo
  if (isEvent(key)) {
    const event = key.slice(2).toLocaleLowerCase();
    dom.removeEventListener(event, value);
  } else {
    setAttribute({ key, undefined, dom });
  }
}

function setAttribute({ key, value, dom }) {
  if (key === 'children') return;
  if (isEvent(key)) {
    // äº‹ä»¶
    const event = key.slice(2).toLocaleLowerCase();
    dom.addEventListener(event, value);
  } else if (key === 'style') {
    // æ ·å¼
    for (let name in value) {
      dom.style[name] = value[name];
    }
  } else if (key === 'className') {
    dom.setAttribute('class', value);
  } else {
    dom[key] = value;
    // textNodeæ²¡æœ‰setAttributeæ–¹æ³•
    dom.setAttribute && dom.setAttribute(key, value);
  }
}

// åˆ›å»ºç»„ä»¶å®ä¾‹
function createComponent(element, internalInstance) {
  const { type, props } = element;
  let component;

  if (type.prototype.render) {
    // ç±»ç»„ä»¶
    component = new type(props);
  } else {
    component = new Didact.Component(props);
    component.constructor = type;
    component.render = () => type(component.props);
  }
  component.__internalInstance = internalInstance;
  return component;
}
export { DidactDOM, Didact };
